<template>
  <div>
    <el-row :gutter="20" style="margin-left: 0px;margin-right: 0px;">
      <el-col :span="2" style="margin-top: 15px;">
          <el-button type="primary" plain size="small" @click="insertpermit = true">添加课程信息</el-button>
          <el-dialog title="添加课程信息" :visible.sync="insertpermit" class='cour-input'>
              <el-form :model="add_form">
                  <el-form-item label="课程ID" :label-width="formLabelWidth">
                      <el-input v-model="add_form.cour_id" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="课程名称" :label-width="formLabelWidth">
                      <el-input v-model="add_form.cour_name" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="课程学分" :label-width="formLabelWidth">
                      <el-input v-model="add_form.credit" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="课程学时" :label-width="formLabelWidth">
                      <el-input v-model="add_form.period" autocomplete="off"></el-input>
                  </el-form-item>
              </el-form>

              <div slot="footer" class="dialog-footer">
              <el-button @click="insertpermit = false;insert_fail()">取 消</el-button>
              <el-button type="primary" @click="insertobj();insertpermit = false">确 定</el-button>
              </div>
          </el-dialog>
      </el-col>
      <el-col :span="22" style="margin-top:15px;" class='cour-search'>
            <div style="float:right">
                <el-input v-model="keywords" placeholder="请输入课程名搜索" style="width:200px;color: #606266;" plain size="small"></el-input>
                <el-button type="primary" @click="clearkeyword" plain size="small">清空</el-button>          
            </div>
      </el-col>
    </el-row>
    <el-row :gutter="20" style="margin-left: 0px;margin-right: 0px;">
      <el-col :span="24" style="margin-top: 15px;">
          <el-card shadow="hover">
              <el-table
                  :data="filtableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                  style="width: 100%"
                  border
                  stripe
                  >
                  <el-table-column
                  v-for="(val,key) in tablelabel"
                  :key="key"
                  :prop="key"
                  :label="val">
                  </el-table-column>
                  <el-table-column label="操作">
                      <template slot-scope="scope">
                          <el-button
                              size="mini"
                              @click="updatepermit = true;handleEdit(scope.$index, scope.row)">编辑
                          </el-button>
                          <el-dialog class='cour-input' title="修改课程信息" :visible.sync="updatepermit">
                              <el-form :model="form">
                                  <el-form-item label="课程学分" :label-width="formLabelWidth">
                                      <el-input v-model="form.credit" autocomplete="off"></el-input>
                                  </el-form-item>
                                  <el-form-item label="课程学时" :label-width="formLabelWidth">
                                      <el-input v-model="form.period" autocomplete="off"></el-input>
                                  </el-form-item>
                              </el-form>

                              <div slot="footer" class="dialog-footer">
                              <el-button @click="updatepermit = false;fail()">取 消</el-button>
                              <el-button type="primary" @click="updateobj();updatepermit = false">确 定</el-button>
                              </div>
                          </el-dialog>
                          <el-button
                              size="mini"
                              type="danger"
                              @click="handleDelete(scope.$index, scope.row);deleteobj()">删除
                          </el-button>
                      </template>
                  </el-table-column>
                </el-table>
                <el-pagination class='cour-input' style="margin-top:15px" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
                    :page-sizes="[4,6,8]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
                    :total="tableData.length">
                </el-pagination>

        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
    name:'Course',
    data() {
        return {
            keywords: '', 
            tableData: [],
            currentPage: 1,
            pagesize: 8,

            tablelabel:{
                'cour_id':'课程ID',
                'cour_name':'课程名',
                'credit':'学分',
                'period':'学时',
            },
            updatepermit:false,
            insertpermit:false,
            formLabelWidth: '120px',
            form: {
                cour_id:'',
                credit:'',
                period:''
            },
            delete_cour_id:'',
            add_form:{
                cour_id:'',
                cour_name:'',
                credit:'',
                period:'',   
            }
        }
    },
    methods: {
        get_tableData(){
            this.$axios.get('http://127.0.0.1:8000/sims_app/select_cour/')
            .then(response=>{
                this.tableData = response.data
            })
        },
         handleSizeChange(size) {
            this.pagesize = size;
        },
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage;
        },
        handleEdit(index, row) {
            this.form.cour_id = row.cour_id;
            console.log(index, row.cour_id);
        },
        handleDelete(index, row) {
            this.delete_cour_id = row.cour_id;
            console.log(index, row.cour_id);
        },
        updateobj(){
            this.$axios.post('http://127.0.0.1:8000/sims_app/update_cour/',
            {cour_id:this.form.cour_id,credit:this.form.credit,period:this.form.period})
            .then(response=>{
                if (response.data.code==200){
                    this.form = {cour_id:'',credit:'',period:''}
                    this.success()
                }
                else{
                    this.$message({
                    message:'存在字段未输入，请重新尝试！',
                    center: true,
                    type: 'error'
                    });
                    this.form = {cour_id:'',credit:'',period:''}
                }
                })
        },
        deleteobj(){
            this.$axios.post('http://127.0.0.1:8000/sims_app/delete_cour/',
            {cour_id:this.delete_cour_id})
            .then(response=>{
                if (response.data.code==200){
                    this.delete_cour_id = ''
                    this.$confirm('此操作将永久删除该课程信息, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                        })
                        .then(() => {
                        this.$message({
                            type: 'success',
                            center: true,
                            message: '删除成功!'
                        });setTimeout(function(){window.location.reload()},1700);
                        })
                        .catch(() => {
                        this.$message({
                            type: 'info',
                            center: true,
                            message: '已取消删除!'
                        });          
                    });
                }
                })
        },
        insertobj(){
            this.$axios.post('http://127.0.0.1:8000/sims_app/insert_cour/',
                {
                    cour_id:this.add_form.cour_id,
                    cour_name:this.add_form.cour_name,
                    credit:this.add_form.credit,
                    period:this.add_form.period,
                })
            .then(response=>{
                if (response.data.code==200){
                    this.add_form = {cour_id:'',cour_name:'',credit:'',period:''}
                    this.insert_success()
                }
                else if (response.data.code==405){
                    this.$message({
                    message:'课程id已存在，添加失败，请重新尝试！',
                    center: true,
                    type: 'error'
                    });
                    this.add_form = {stu_id:'',stu_name:'',stu_sex:'',stu_class:'',stu_major:''}
                }
                else{
                    this.$message({
                    message:'存在字段未输入，请重新尝试！',
                    center: true,
                    type: 'error'
                    });
                    this.add_form = {stu_id:'',stu_name:'',stu_sex:'',stu_class:'',stu_major:''}     
                }
                })
        },
        fail() {
            this.$message({
            message:'已取消修改！',
            center: true,
            type: 'info'
            });
        },
        success() {
            this.$message({
            message: '修改成功！',
            center: true,
            type: 'success'
            });
            setTimeout(function(){window.location.reload()},1700);
        },
        insert_fail() {
            this.$message({
            message:'已取消添加！',
            center: true,
            type: 'info'
            });
        },
        insert_success() {
            this.$message({
            message: '添加成功！',
            center: true,
            type: 'success'
            });
            setTimeout(function(){window.location.reload()},1700);
        },
        clearkeyword(){
            this.keywords = ''
        }
    },
    computed: {
        filtableData(){
            return this.tableData.filter((item)=>{
                return item.cour_name.indexOf(this.keywords) !== -1
            })
        }
    },
    mounted() {
        this.get_tableData()
    },
}
</script>

<style>
    .cour-input .el-input__inner{
        color: #606266
    }
    .cour-search .el-input__inner{
        background-color: #FFF;
        color: #606266;
    }
</style>